import React, { useState, useEffect } from 'react'
import { useHistory, useLocation } from 'react-router-dom'
import {
  CButton,
  CCollapse,
  CBadge,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CDataTable,
  CRow,
  CPagination
} from '@coreui/react'

import usersData from './UsersData'
import {Configuration} from '../../Configuration';
import TheHeader from  '../../containers/TheHeader'
import TheSidebar from  '../../containers/TheSidebar'
import TheContent from  '../../containers/TheContent'
import { CSVLink, CSVDownload } from "react-csv";

// const [items, setItems] = useState(usersData)

const getBadge = (status)=>{
  switch (status) {
    case 'Active': return 'success'
    case 'Inactive': return 'secondary'
    case 'Pending': return 'warning'
    case 'Banned': return 'danger'
    default: return 'primary'
  }
}

const Users = (props) => {
  const history = useHistory()
  const queryPage = useLocation().search.match(/page=([0-9]+)/, '')
  const currentPage = Number(queryPage && queryPage[1] ? queryPage[1] : 1)
  const [page, setPage] = useState(currentPage)
  const [details, setDetails] = useState([])

  const pageChange = newPage => {
    currentPage !== newPage && history.push(`/users?page=${newPage}`)
  }
  const toggleDetails = (index) => {
    const position = details.indexOf(index)
    let newDetails = details.slice()
    if (position !== -1) {
      newDetails.splice(position, 1)
    } else {
      newDetails = [...details, index]
    }
    setDetails(newDetails)
  }
  const fields = [
    { key: 'UserName', _style: { width: '40%'} },
    'Employee',
    { key: 'Designation', _style: { width: '20%'} },
    { key: 'PlaceofWorking', _style: { width: '20%'} },
    { key: 'Mandal', _style: { width: '20%'} },
    { key: 'District', _style: { width: '20%'} },
    { key: 'DateofBirth', _style: { width: '20%'} },
    { key: 'Caste', _style: { width: '20%'} },
    { key: 'NativeDistrict', _style: { width: '20%'} },
    { key: 'Qualification', _style: { width: '20%'} },
    { key: 'VASAppointmentDate', _style: { width: '20%'} },
    { key: 'VASWorkingFrom', _style: { width: '20%'} },

    
    { key: 'Password', _style: { width: '0%'} },

    {
      key: 'show_details',
      label: '',
      _style: { width: '1%' },
      sorter: false,
      filter: false
    }
  ]

  const  headers = [
    { label: 'UserName', key: 'UserName' },
    { label: 'Employee', key: 'Employee' },
    { label: 'Designation', key: 'Designation' },
    { label: 'PlaceofWorking', key: 'PlaceofWorking' },
    { label: 'Mandal', key: 'Mandal' },
    { label: 'District', key: 'District' },
    { label: 'DateofBirth', key: 'DateofBirth' },
    { label: 'Caste', key: 'Caste' },
    { label: 'NativeDistrict', key: 'NativeDistrict' },
    { label: 'Qualification', key: 'Qualification' },
    { label: 'VASAppointmentDate', key: 'VASAppointmentDate' },
    { label: 'VASWorkingFrom', key: 'VASWorkingFrom' },
  ];
  useEffect(() => {
    currentPage !== page && setPage(currentPage)
  }, [currentPage, page])
  useEffect(() => {
    if(!props.fetched) {
      getUsers();
    }
    console.log('mount it!');
  }, []);
  const config = {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      "Access-Control-Allow-Origin": "http://localhost:5001",
    },
  };
  const home = () => {
    props.history.push("/dashboard");
   }

  const  getUsers = async() => {
    const apiUrl = Configuration.SYSTEM_SETTINGS.API_URL+'/users';
    debugger;
   await fetch(apiUrl,config)
      .then((response) => response.json())
      .then((data) => 
      Configuration.UserList = data.response
      );
      console.log("Data" + JSON.stringify(Configuration.UserList ));
  }

  
  return (
    <>
    
  
    <CRow>
    <CCol xl={12}>
      <CCard>
        <CCardHeader >
          <CRow>
            <CCol>
            <h5>Users List: </h5> 
            </CCol>

            <CCol>
            <small className="text-muted"> 
          
            &nbsp;&nbsp;&nbsp;&nbsp;
          <CSVLink data={Configuration.UserList}  filename={"UserList.csv"} className="btn btn-primary float-right"  headers ={headers} >Download CSV</CSVLink>
          </small>
            </CCol>
          </CRow>
          
      
        </CCardHeader>
        <CCardBody>
    <CDataTable
      items={Configuration.UserList}
      fields={fields}
      //columnFilter
      tableFilter
      itemsPerPageSelect
      itemsPerPage={5}
      hover
      sorter
      pagination
      activePage={page}
      clickableRows
      onRowClick={(item) => history.push(`/users/${item.Userid}`)}
      scopedSlots = {{
        'status':
          (item)=>(
            <td>
              <CBadge color={getBadge(item.Status)}>
                {item.status}
              </CBadge>
            </td>
          ),
        'show_details':
          (item, index)=>{
            return (
              <td className="py-2">
                <CButton
                  color="primary"
                  variant="outline"
                  shape="square"
                  size="sm"
                  onClick={()=>{toggleDetails(index)}}
                >
                  {details.includes(index) ? 'Hide' : 'Show'}
                </CButton>
              </td>
              )
          },
        'details':
            (item, index)=>{
              return (
              <CCollapse show={details.includes(index)}>
                <CCardBody>
                  <h4>
                    {item.UserName}
                  </h4>
                  <p className="text-muted">User since: {item.Country}  {item.Userid}</p>
                  <CButton size="sm" color="success">
                    Approve
                  </CButton>
                  <CButton size="sm" color="danger" className="ml-1">
                    Reject
                  </CButton>
                </CCardBody>
              </CCollapse>
            )
          }
      }}
    />
           </CCardBody>
        </CCard>
      </CCol>
    </CRow>

    </>
  )
}

export default Users
